.intro__container {
    position: relative;
    max-width: 3000px;
    margin: 0 auto;

    &::after {
        content: "";
        display: block;
        width: 100%;
        padding-top: 43.8%;

        @include xxs {
            padding-top: 128.5%;
        }
    }

    picture {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}
