.section {
    margin-bottom: 170px;

    @include md {
        margin-bottom: 120px;
    }

    @include xs {
        margin-bottom: 90px;
    }
}

.section__title {
    margin: 0 0 44px 0;
    font-weight: 600;
    font-size: 75px;
    line-height: 116%;

    @include md {
        font-size: 50px;
    }

    @include xs {
        margin-bottom: 35px;
        font-size: 35px;
    }

    @include r(370px) {
        font-size: 30px;
    }
}

.js-input.error {
    color: var(--color-red, #f32a2a);
    border-color: var(--color-red, #f32a2a);
}

.js-input {
    border-top: none;
    border-right: none;
    border-left: none;
    border-radius: 0;
}
