:root {
  --color-dark: #090909;
  --color-green: #41a62b;
  --color-green-bg: #40a22b;
  --color-white: #fff;
  --color-white-fc: #fcfcfc;
  --color-white-fb: #fbfbfb;
  --color-grey: #a1a1a1;
  --color-grey-13: #131313;
  --color-red: #f32a2a;
}

@font-face {
  font-family: "Grtsk Peta";
  src: url("../fonts/GrtskPeta-Semibold.woff2") format("woff2"), url("../fonts/GrtskPeta-Semibold.woff") format("woff"), url("../fonts/GrtskPeta-Semibold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Grtsk Peta";
  src: url("../fonts/GrtskPeta-Regular.woff2") format("woff2"), url("../fonts/GrtskPeta-Regular.woff") format("woff"), url("../fonts/GrtskPeta-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
.section {
  margin-bottom: 170px;
}
@media (max-width: 1200px) {
  .section {
    margin-bottom: 120px;
  }
}
@media (max-width: 767px) {
  .section {
    margin-bottom: 90px;
  }
}

.section__title {
  margin: 0 0 44px 0;
  font-weight: 600;
  font-size: 75px;
  line-height: 116%;
}
@media (max-width: 1200px) {
  .section__title {
    font-size: 50px;
  }
}
@media (max-width: 767px) {
  .section__title {
    margin-bottom: 35px;
    font-size: 35px;
  }
}
@media only screen and (max-width: 370px) {
  .section__title {
    font-size: 30px;
  }
}

.js-input.error {
  color: var(--color-red, #f32a2a);
  border-color: var(--color-red, #f32a2a);
}

.js-input {
  border-top: none;
  border-right: none;
  border-left: none;
  border-radius: 0;
}

/*===== global styles =====*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  min-width: 320px;
  scrollbar-gutter: stable;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--color-dark, #1a1a1a);
  font-family: "Grtsk Peta", sans-serif;
  font-weight: 400;
  background-color: var(--color-white, #fff);
}

body.lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1580px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.header {
  background-color: var(--color-green-bg, #40a22b);
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
}

.header__container {
  max-width: 1734px;
  min-height: 137px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 991px) {
  .header__container {
    min-height: 137px;
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .header__container {
    display: block;
    padding-top: 18px;
    padding-bottom: 24px;
  }
}

.header__logo {
  display: block;
  width: 425px;
  height: auto;
  transform: translateY(-10px);
}
@media (max-width: 991px) {
  .header__logo {
    width: 244px;
    transform: translateY(-5px);
  }
}
@media (max-width: 767px) {
  .header__logo {
    margin: 0 auto 18px;
  }
}

.header__info {
  display: flex;
  flex-direction: column;
  gap: 5px 0;
}

.header__work {
  font-weight: 400;
  font-size: 25px;
  letter-spacing: 0.03em;
  text-align: right;
  color: var(--color-white-fc, #fcfcfc);
  transition: color 0.2s ease-out;
}
@media (max-width: 767px) {
  .header__work {
    font-size: 16px;
    text-align: center;
  }
}
@media (hover: hover) {
  .header__work:hover {
    color: #51c038;
  }
}

.header__tel {
  display: inline-block;
  font-weight: 600;
  font-size: 35px;
  letter-spacing: 0.03em;
  text-align: right;
  color: var(--color-white-fc, #fcfcfc);
  text-decoration: none;
  transition: color 0.2s ease-out;
}
@media (hover: hover) {
  .header__tel:hover {
    color: #51c038;
  }
}
@media (max-width: 767px) {
  .header__tel {
    font-size: 23px;
    text-align: center;
  }
}

.main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.intro__container {
  position: relative;
  max-width: 3000px;
  margin: 0 auto;
}
.intro__container::after {
  content: "";
  display: block;
  width: 100%;
  padding-top: 43.8%;
}
@media (max-width: 575px) {
  .intro__container::after {
    padding-top: 128.5%;
  }
}
.intro__container picture {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
}
.intro__container img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.form {
  position: relative;
  z-index: 2;
  margin-bottom: 22px;
  padding: 44px 91px 60px;
  background-color: var(--color-green-bg, #40a22b);
  background: linear-gradient(90deg, #194011 0.26%, #40a32a 48.7%, #194011 100%);
  color: var(--color-white-fc, #fcfcfc);
  overflow: hidden;
  transform: translateY(-30%);
}
.form input,
.form button {
  padding: 0;
  background-color: transparent;
  appearance: none;
  outline: none;
}
.form input,
.form input::placeholder,
.form button {
  color: inherit;
}
@media (max-width: 1600px) {
  .form {
    padding: 32px 40px 50px;
    transform: translateY(-20%);
  }
}
@media (max-width: 991px) {
  .form {
    margin-bottom: 42px;
    transform: translateY(-5%);
  }
}
@media (max-width: 575px) {
  .form {
    padding: 32px 20px 49px;
  }
}

.form__title {
  margin: 0 0 12px 0;
  font-weight: 600;
  font-size: 40px;
  line-height: 108%;
}
@media (max-width: 1300px) {
  .form__title {
    font-size: 34px;
  }
}
@media (max-width: 991px) {
  .form__title {
    font-size: 28px;
  }
}
@media (max-width: 767px) {
  .form__title {
    margin: 0 0 8px 0;
  }
}

.form__subtitle {
  margin: 0 0 32px 0;
  font-weight: 400;
  font-size: 30px;
  line-height: 116%;
}
@media (max-width: 1200px) {
  .form__subtitle {
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .form__subtitle {
    margin-bottom: 13px;
    font-size: 18px;
  }
}

.form__form {
  display: flex;
  gap: 0 55px;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 42px;
}
@media (max-width: 1600px) {
  .form__form {
    gap: 0 30px;
  }
}
@media (max-width: 991px) {
  .form__form {
    flex-direction: column;
    align-items: flex-start;
    gap: 26px 0;
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .form__form {
    margin-bottom: 13px;
  }
}

.form__input {
  display: block;
  width: 100%;
  height: 52px;
  font-weight: 400;
  font-size: 20px;
  line-height: 128%;
  letter-spacing: -0.03em;
  border: none;
  border-bottom: 2px solid var(--color-white-fc, #fcfcfc);
}
.form__input.error {
  color: var(--color-red, #f32a2a);
  border-bottom-color: var(--color-red, #f32a2a);
}
@media (max-width: 1200px) {
  .form__input {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    font-size: 20px;
  }
}
@media (max-width: 991px) {
  .form__input {
    max-width: 550px;
  }
}
@media (max-width: 767px) {
  .form__input {
    max-width: 100%;
    height: 44px;
    font-size: 18px;
  }
}

.form__submit {
  flex: 0 0 432px;
  display: block;
  min-width: 432px;
  min-height: 57px;
  padding: 9px 28px 14px;
  font-weight: 600;
  font-size: 25px;
  line-height: 116%;
  text-align: center;
  border: 1px solid var(--color-white-fc, #fcfcfc);
  border-radius: 47px;
  cursor: pointer;
  transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out;
}
@media (hover: hover) {
  .form__submit:hover {
    background-color: var(--color-white-fc, #fcfcfc);
    color: #41a62b;
  }
}
@media (max-width: 1300px) {
  .form__submit {
    flex: 0 0 320px;
    min-width: 320px;
    font-size: 20px;
  }
}
@media (max-width: 991px) {
  .form__submit {
    flex: unset;
  }
}
@media (max-width: 767px) {
  .form__submit {
    width: 100%;
    margin-top: 13px;
    padding: 8px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: unset;
    min-height: 47px;
    font-size: 20px;
  }
}
@media only screen and (max-width: 370px) {
  .form__submit {
    font-size: 18px;
  }
}

.form__policy {
  margin: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 125%;
  letter-spacing: -0.03em;
  opacity: 0.8;
}
@media (max-width: 767px) {
  .form__policy {
    font-size: 14px;
    line-height: 121%;
  }
}

.form__show {
  border: none;
  cursor: pointer;
  text-decoration: underline;
}
@media (hover: hover) {
  .form__show:hover {
    opacity: 1;
  }
}

.form__bg {
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 100%;
  max-width: 1920px;
  height: 100%;
  transform: translateX(-50%);
  z-index: 1;
  pointer-events: none;
}
.form__bg img {
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  width: 100%;
  height: auto;
  object-fit: cover;
}
@media (max-width: 991px) {
  .form__bg {
    width: 56%;
    max-width: 560px;
    max-width: unset;
    left: unset;
    transform: none;
    right: 0;
  }
}
@media (max-width: 767px) {
  .form__bg {
    width: 100%;
    max-width: 430px;
  }
}
@media (max-width: 575px) {
  .form__bg {
    max-width: 390px;
  }
}

.quiz {
  margin-bottom: 110px;
  color: #fff;
}
@media (max-width: 1600px) {
  .quiz {
    margin-bottom: 120px;
  }
}
@media (max-width: 767px) {
  .quiz {
    margin-bottom: 90px;
  }
}
.quiz input,
.quiz button {
  padding: 0;
  background-color: transparent;
  appearance: none;
  outline: none;
}
.quiz input,
.quiz input::placeholder,
.quiz button {
  color: inherit;
}

.quiz__title {
  margin-bottom: 51px;
  font-weight: 600;
  font-size: 55px;
  line-height: 116%;
  text-align: center;
  color: var(--color-black, #090909);
}
.quiz__title span {
  display: block;
  width: 100%;
  font-weight: 400;
}
@media (max-width: 1600px) {
  .quiz__title {
    font-size: 50px;
  }
}
@media (max-width: 1300px) {
  .quiz__title {
    margin-bottom: 41px;
    font-size: 40px;
  }
}
@media (max-width: 767px) {
  .quiz__title {
    margin-bottom: 31px;
    font-size: 28px;
    text-align: left;
  }
}

.quiz__form {
  position: relative;
  overflow: hidden;
  min-height: 564px;
  padding: 52px 63px 133px;
  background: url("../images/quiz/quiz-bg.png") center right/cover no-repeat;
  background-image: image-set(url("../images/quiz/quiz-bg@2x.webp") type("image/webp") 2x, url("../images/quiz/quiz-bg.webp") type("image/webp") 1x, url("../images/quiz/quiz-bg@2x.png") 2x, url("../images/quiz/quiz-bg.png") 1x);
}
@media (max-width: 1600px) {
  .quiz__form {
    padding-bottom: 230px;
  }
}
@media (max-width: 991px) {
  .quiz__form {
    min-height: 760px;
    padding: 40px 35px 200px;
    background: url("../images/quiz/quiz-bg-mob.png") center bottom/cover no-repeat;
    background-image: image-set(url("../images/quiz/quiz-bg-mob@2x.webp") type("image/webp") 2x, url("../images/quiz/quiz-bg-mob.webp") type("image/webp") 1x, url("../images/quiz/quiz-bg-mob@2x.png") 2x, url("../images/quiz/quiz-bg-mob.png") 1x);
  }
}
@media (max-width: 767px) {
  .quiz__form {
    min-height: 840px;
    padding: 35px 20px 200px;
  }
}

.quiz__boilers {
  position: absolute;
  bottom: -19.7%;
  right: -7.6%;
  z-index: 1;
  width: 56.5%;
  aspect-ratio: 847/873;
}
.quiz__boilers img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (max-width: 1600px) {
  .quiz__boilers {
    bottom: -12.7%;
    width: 48.5%;
  }
}
@media (max-width: 991px) {
  .quiz__boilers {
    bottom: -9%;
    right: 50%;
    width: 60%;
    transform: translateX(50%);
  }
}
@media (max-width: 767px) {
  .quiz__boilers {
    width: 100%;
    bottom: -11%;
  }
}
@media (max-width: 575px) {
  .quiz__boilers {
    bottom: -5.7%;
    bottom: -90px;
    width: 134%;
  }
}
@media only screen and (max-width: 450px) {
  .quiz__boilers {
    bottom: -70px;
  }
}
@media only screen and (max-width: 400px) {
  .quiz__boilers {
    bottom: -60px;
  }
}

.quiz__tab {
  position: relative;
  z-index: 3;
  max-width: 825px;
  display: none;
}
.quiz__tab.active {
  display: block;
}

.quiz__num {
  display: inline-block;
  margin: 0 0 29px 0;
  padding: 10px 25px 12px 25px;
  min-height: 45px;
  border-radius: 60px;
  font-weight: 400;
  font-size: 20px;
  line-height: 116%;
  text-align: center;
  color: #000;
  background-color: #fff;
}
@media (max-width: 991px) {
  .quiz__num {
    display: block;
    width: max-content;
    min-height: 43px;
    margin: 0 auto 28px;
    font-size: 18px;
  }
}

.quiz__name {
  margin: 0 0 46px 0;
  font-weight: 400;
  font-size: 30px;
  line-height: 116%;
  text-align: left;
  color: #fff;
}
@media (max-width: 991px) {
  .quiz__name {
    margin: 0 0 30px 0;
    font-size: 24px;
  }
}
@media (max-width: 767px) {
  .quiz__name {
    margin: 0 0 25px 0;
    font-size: 22px;
    text-align: center;
  }
}

.quiz__inputs {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto;
  gap: 23px 19px;
  margin-bottom: 60px;
