.policy {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 4;
  width: 100%;
  height: 100%;
  display: none;
}
.policy.active {
  display: block;
}
.policy.active .policy__overlay {
  animation: fadeIn 0.4s forwards;
}
.policy.active .policy__close {
  animation: fadeIn 0.4s forwards;
}
.policy.active .policy__wrapper {
  animation: fadeIn 0.4s forwards;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.policy__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  cursor: pointer;
  opacity: 0;
}

